
.title {
    font-size: 38px;
    margin : 40px 0;
    font-weight: bold;
  

}



.btn {

    position: relative;
    border: none;
    display: block;
    margin:0px;
    padding: 15px 30px;
    border-radius: 15px;
    font-family: "paybooc-Light", sans-serif;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    text-decoration: none;
    font-weight: 600;
    transition: 0.25s;
    background-color: #a3a1a1;
    color: #e3dede;
}